/* inputs */
.radio-label {
  position: relative; }

.input-group input[type="radio"],
.input-group input[type="checkbox"] {
  display: none; }

input[type="radio"] + label,
input[type="checkbox"] + label {
  display: flex;
  margin: 0;
  align-items: center;
  cursor: pointer;
  position: relative; }

input[type="radio"] + label:before {
  content: '';
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: block;
  margin-right: 10px; }

input[type="radio"]:checked + label:after {
  content: '';
  background: #45378c;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  display: block;
  position: absolute;
  left: 4px;
  top: 5px; }

input[type="checkbox"] + label:before {
  content: '';
  border: 1px solid #ddd;
  width: 18px;
  height: 18px;
  display: block;
  margin-right: 10px; }

input[type="checkbox"]:checked + label:before {
  background: #45378c url(../img/checkbox.svg) center no-repeat;
  background-size: 11px 8px;
  border-color: #45378c; }

